<template>
  <div>
    <pre><code v-html="html"></code></pre>
  </div>
</template>

<script>
export default {
  props: ['headers', 'rows'],
  data() {
    return {
      html: '',
    };
  },
  mounted() {
    this.generate();
  },
  computed: {
    completeRowData() {
      return this.rows.map((r) => {
        const result = {};
        this.headers.forEach((f, idx) => {
          const key = f.name || `field-${idx}`;
          result[key] = r[`${f.id}`];
        });
        return result;
      });
    },
  },
  methods: {
    generate() {
      this.html = JSON.stringify(this.completeRowData, null, 2);
    },
  },
  watch: {
    headers: {
      deep: true,
      handler() {
        this.generate();
      },
    },
    rows() {
      this.generate();
    },
  },
};
</script>

