import Chance from 'chance';
import * as dayjs from 'dayjs';
import AdvancedFormat from 'dayjs/plugin/advancedFormat';

dayjs.extend(AdvancedFormat);
const chance = new Chance();

export function generate(action, options) {
  if (action === 'date') {
    const aDate = chance[action]({ american: false });
    if (options.format) {
      const theDate = dayjs(aDate).format(options.format);
      return theDate;
    }
    return aDate.toString();
  }
  if (action === 'bit') {
    return chance.integer({ min: 0, max: 1 });
  }
  if (action === 'name') {
    const gender = options.gender || chance.gender();
    const nationality = options.nationality || undefined;
    const fname = chance.first({ gender });
    const lname = chance.last({ nationality });
    if (options.format === 'fname lname') {
      return `${fname} ${lname}`;
    }
    if (options.format === 'lname fname') {
      return `${lname} ${fname}`;
    }
    if (options.format === 'prefix fname lname') {
      const prefix = chance.prefix({ gender });
      return `${prefix} ${fname} ${lname}`;
    }
    return '';
  }
  if (action === 'first') {
    const updatedOptions = { ...options };
    if (options.gender === 'all') {
      delete updatedOptions.gender;
    }
    return chance.first(updatedOptions);
  }
  if (action === 'ip') {
    if (options.ipv6) {
      return chance.ipv6();
    }
    return chance.ip();
  }
  if (action === 'animal') {
    if (options.type === 'any') {
      return chance.animal();
    }
    return chance.animal(options);
  }
  if (action === 'chanceJS') {
    const { action: theAction, options: optionString } = options;
    let theOptions = {};
    try {
      theOptions = JSON.parse(optionString);
      return chance[theAction](theOptions);
    } catch (err) {
      theOptions = {};
    }
    return undefined;
  }
  return chance[action](options);
}

function primitives() {
  return [{
    dataType: 'bool',
    tags: ['primitive'],
    options: [{
      property: 'likelihood',
      label: 'likelihood',
      type: 'number',
      default: 50,
      min: 0,
      max: 100,
      description: 'likelihood of returning TRUE',
    }],
  }, {
    dataType: 'bit',
    tags: ['primitive'],
    options: [],
  }];
}

