const presetDataTypes = [
  ...primitives(),
  ...textual(),
  ...numbers(),
  ...temporal(),
  ...people(),
  ...name(),
  ...things(),
  ...location(),
  ...concept(),
];

function custom() {
  return [{
    dataType: 'chanceJS',
    tags: ['custom'],
    options: [{
      property: 'action',
      label: 'action',
      type: 'string',
      default: 'bool',
      description: 'method to invoke (see chanceJS documentation)',
    }, {
      property: 'options',
      label: 'options',
      type: 'json',
      default: '{}',
      description: 'JSON string of the options (see chanceJS documentation)',
      placeholder: '{"key":value, ...}',
    }],
  }];
}

let singletonDataTypes = null;
export function dataTypes() {
  if (!singletonDataTypes) {
    singletonDataTypes = [
      ...presetDataTypes,
      ...custom(),
    ];
  }
  return singletonDataTypes;
}
