<template>
  <div class="data-generator">
    <!-- dynamic table -->
    <!-- [Order] name | data type | data | options -->
    <h1>
      Data Generator
    </h1>
    <hr>
    <div>
      <table class="zebra-rows sticky-header fixed-75">
        <thead>
          <tr>
            <th class="width-50">
              Order
            </th>
            <th class="width-200">
              Field name
            </th>
            <th class="width-150">
              Data type
            </th>
            <th class="width-300">
              Parameters
            </th>
            <th>
              Sample output
            </th>
            <th class="width-75">
              Actions
            </th>
          </tr>
        </thead>
        <draggable v-model="fields" tag="tbody" handle=".js-handle">
          <template v-for="row in fields">
            <tr :key="row.id">
              <td class="js-handle">
                <span class="codicon codicon-three-bars"></span>
              </td>
              <td>
                <input type="text" v-model="row.name" placeholder="field name" />
              </td>
              <td>
                <select v-model="row.dataType" @change="updateOptionsForRow(row)">
                  <option disabled value="">Please select one</option>
                  <option v-for="dataType in dataTypes" :key="dataType.dataType" :value="dataType">
                    {{ dataType.dataType }}
                  </option>
                </select>
              </td>
              <td>
                <template v-if="row.dataType">
                  <template v-for="option in row.dataType.options">
                    <div :key="option.property">
                      <label :title="option.description">
                        <span class="cursor-info">{{ option.label }}</span>
                        <template v-if="Array.isArray(option.type)">
                          <select v-model="row.options[option.property]">
                            <option v-for="choice in option.type" :key="choice">
                              {{ choice }}
                            </option>
                          </select>
                        </template>
                        <template v-else-if="option.type === 'boolean'">
                          <input type="checkbox" v-model="row.options[option.property]">
                        </template>
                        <template v-else-if="option.type === 'number'">
                          <input type="number" v-model.number="row.options[option.property]">
                        </template>
                        <template v-else-if="option.type === 'json'">
                          <textarea v-model="row.options[option.property]" :placeholder="option.placeholder"/>
                        </template>
                        <template v-else>
                          <input :type="option.type" v-model="row.options[option.property]">
                        </template>
                      </label>
                    </div>
                  </template>
                </template>
              </td>
              <td>
                <template v-if="row.dataType">
                  {{ generateSample(row) }}
                </template>
              </td>
              <td>
                <button @click="deleteRow(row.id)">
                  <span class="codicon codicon-trash"></span>
                </button>
              </td>
            </tr>
          </template>
        </draggable>
      </table>
      <div class="inline-form">
        <button @click="addRow">
          add
        </button>
        <label>
          <input type="number" min="1" max="100" v-model="addQuantity">
          <span>field(s)</span>
        </label>
      </div>
    </div>
    <hr>
    <div>
      <div class="inline-form">
        <button @click="generate">
          generate
        </button>
        <label>
          <input type="number" min="1" max="100" v-model="generateQuantity">
          <span>row(s)</span>
        </label>
        <span>&nbsp;</span>
        <template v-for="option in ['default', 'csv', 'json']">
          <label :key="option">
            <input type="radio" v-model="outputFormat" :value="option">
            {{ option }}
          </label>
        </template>
      </div>
      <div v-if="orderedOutput.length">
        <OutputFormatter :format="outputFormat" :headers="fields" :rows="orderedOutput"/>
      </div>
    </div>
    <hr>
    <div class="flex-row justify-space-between">
      <div>
        <small>Random data powered by ChanceJS <a href="https://chancejs.com/index.html" target="_blank">https://chancejs.com/index.html</a></small>
        <br>
        <small>Date formatting powered by DayJS <a href="https://day.js.org/docs/en/display/format" target="_blank">https://day.js.org/docs/en/display/format</a></small>
      </div>
      <div>
        <label>
          debug mode
          <input type="checkbox" v-model="debugMode">
        </label>
      </div>
    </div>
    <hr>
    <div v-if="debugMode">
      <h4>developer region</h4>
      <pre><code>{{ fields }}</code></pre>
      <hr>
      <div class="flex-row align-bottom">
        <textarea v-model="importData" cols="100" rows="5" placeholder='
        JSON e.g: [{"name":"myFieldName","dataType":"bool"},...]
        OR
        CSV e.g: myFieldName,bool
        '/>
        <button @click="importSchema">import</button>
      </div>
    </div>
  </div>
</template>

<script>
import draggable from 'vuedraggable';
import OutputFormatter from '@/components/outputFormats/OutputFormatter.vue';
import * as Dice from '@/core/dice';
import * as Hashing from '@/core/hashing';

function generateValue(dataType, options) {
  if (!dataType) { return null; }
  return Dice.generate(dataType, options);
}

export default {
  name: 'DataGenerator',
  components: {
    draggable,
    OutputFormatter,
  },
  data() {
    return {
      fields: [],
      dataTypes: Dice.dataTypes(),
      output: [],
      generateQuantity: 5,
      addQuantity: 1,
      outputFormat: 'default',
      debugMode: false,
      importData: '',
    };
  },
  computed: {
    orderedOutput() {
      return this.output;
    },
  },
  created() {
    this.addRows(1);
  },
  methods: {
    addRow() {
      this.addRows(this.addQuantity);
    },
    addRows(quantity) {
      const ms = new Date().getUTCMilliseconds();
      for (let i = 0; i < quantity; i += 1) {
        this.fields.push({
          id: ms + i,
          name: '',
          dataType: null,
          options: {}, // will be populated by updateOptionsForRow()
          sample: '',
          optionsHash: '',
        });
      }
    },
    deleteRow(rowId) {
      const idx = this.fields.findIndex((f) => f.id === rowId);
      this.fields.splice(idx, 1);
      this.output.forEach((item) => {
        this.$delete(item, `${rowId}`);
      });
      if (this.fields.length === 0) {
        this.output = [];
      }
    },
    updateOptionsForRow(row) {
      const options = {};
      row.dataType.options.forEach((o) => {
        options[o.property] = o.default;
        if (o.default === null) {
          options[o.property] = undefined;
        }
      });

      this.$set(row, 'options', options);
    },
    importSchema() {
      let data = [];
      try {
        data = JSON.parse(this.importData);
      } catch {
        data = this.importData.split('\n').filter((line) => !!line).map((line) => {
          const [name, dataType] = line.split(',');
          return {
            name,
            dataType,
          };
        });
      }
      const numberOfFields = data.length;
      const newFields = [];
      this.$set(this, 'fields', newFields);
      this.addRows(numberOfFields);
      data.forEach(({ name, dataType }, idx) => {
        const theDataType = Dice.dataTypes().find((d) => d.dataType === dataType);
        this.fields[idx].name = name;
        this.fields[idx].dataType = theDataType;
        this.updateOptionsForRow(this.fields[idx]);
      });
    },
    generateSample(row) {
      const optionsHash = Hashing.hash(`${JSON.stringify(row.dataType)}-${JSON.stringify(row.options)}`);
      if (row.optionsHash === optionsHash) { return row.sample; }

      const maybeDataType = row.dataType ? row.dataType.dataType : null;
      const value = generateValue(maybeDataType, row.options);

      const rowToUpdate = row;
      rowToUpdate.optionsHash = optionsHash;
      rowToUpdate.sample = value;
      return value;
    },
    generate() {
      this.$set(this, 'output', []);

      for (let i = 0; i < this.generateQuantity; i += 1) {
        const item = {};
        this.fields.forEach((f) => {
          const maybeDataType = f.dataType ? f.dataType.dataType : null;
          const value = generateValue(maybeDataType, f.options);
          item[`${f.id}`] = value;
        });
        this.output.push(item);
      }
    },
  },
};
</script>

