<template>
  <div>
    <div v-pre>
      Uses Mustache for template rendering
      Data structure: {
        headers: ['.',],
        rows: [{
          _fields: ['.'],
          headerFieldName: '.',
        }],
      }
      <pre>
{{#headers}} {{title}} {{/headers}}
<br>
awdawdawd
{{#rows}}
  {{#fields}}{{.}}{{/fields}}
{{/rows}}
      </pre>
    </div>
    <hr>
    <textarea v-model="templateHtml" rows="10"></textarea>
    <hr>
    <div v-html="outputHtml"></div>
  </div>
</template>

<script>
import Mustache from 'mustache';

export default {
  props: ['headers', 'rows'],
  data() {
    return {
      outputHtml: '',
      templateHtml: '',
    };
  },
  mounted() {
    this.generate();
  },
  methods: {
    generate() {
      const headers = this.headers.map((f, idx) => ({
        field: `${f.id}`,
        title: f.name || `field-${idx}`,
      }));

      const rows = this.rows.map((r) => {
        const result = {};
        const fields = [];
        this.headers.forEach((f, idx) => {
          const key = f.name || `field-${idx}`;
          result[key] = r[`${f.id}`];
          fields.push(r[`${f.id}`]);
        });
        result.fields = fields;
        return result;
      });

      try {
        const output = Mustache.render(this.templateHtml, {
          headers,
          rows,
        });
        this.outputHtml = output;
      } catch (err) {
        // ignore
      }
    },
  },
  watch: {
    headers: {
      deep: true,
      handler() {
        this.generate();
      },
    },
    rows() {
      this.generate();
    },
    templateHtml() {
      this.generate();
    },
  },
};
</script>

