<template>
  <div>
    <div class="flex-column align-end">
      <a :href="csvDownloadLink" target="_blank" download="generated-data.csv">download .csv file</a>
    </div>
    <pre><code>{{ csvOutput }}</code></pre>
  </div>
</template>

<script>
import * as Convertors from '@/core/convertors';

export default {
  props: ['headers', 'rows'],
  data() {
    return {
      csvOutput: '',
      csvDownloadLink: '',
    };
  },
  mounted() {
    this.generate();
  },
  methods: {
    generate() {
      const headers = this.headers.map((f, idx) => ({
        field: `${f.id}`,
        title: f.name || `field-${idx}`,
      }));

      Convertors.toCsv(this.rows, { keys: headers }).then(this.updateWithCsvData);
    },
    updateWithCsvData(csv) {
      this.$set(this, 'csvOutput', csv);

      const formBlob = new Blob([this.csvOutput], { type: 'text/csv' });
      this.csvDownloadLink = window.URL.createObjectURL(formBlob);
    },
  },
  watch: {
    headers: {
      deep: true,
      handler() {
        this.generate();
      },
    },
    rows() {
      this.generate();
    },
  },
};
</script>

<style lang="scss" scoped>
.flex-column {
  display: flex;
  flex-direction: column;
}

.align-end {
  align-items: flex-end;
}
</style>
