<script>
import OpinionatedTable from './OpinionatedTable.vue';
import Templated from './Templated.vue';
import Csv from './Csv.vue';
import Json from './Json.vue';

export default {
  functional: true,
  render(createElement, context) {
    function appropriateFormatter(outputFormat) {
      switch (outputFormat) {
        case 'csv': return Csv;
        case 'json': return Json;
        case 'template': return Templated;
        case 'table':
        default: return OpinionatedTable;
      }
    }

    return createElement(
      appropriateFormatter(context.props.format),
      context.data,
      context.children,
    );
  },
};
</script>
