import * as json2csv from 'json-2-csv';

export function toCsv(arrayOfJsonObjects, userOptions) {
  const options = {
    ...userOptions,
  };
  return new Promise((resolve, reject) => {
    json2csv.json2csv(arrayOfJsonObjects, (err, csv) => {
      if (err) {
        return reject(err);
      }
      return resolve(csv);
    }, options);
  });
}

export function dummy() { return true; }
